<template>
    <div :id="element.uuid">
        <v-container>
            <v-card outlined>
                <v-toolbar v-if="!isDatailView" :color="colorToolbar" dark hide-on-scroll height="30px" class="drag-handle">
                    <v-hover v-slot="{ hover }">
                        <v-btn icon @click="shoeModeDeclaration">
                            <v-icon>{{ iselementOpenClose ? (hover? 'mdi-chevron-double-left' :'mdi-chevron-double-right') : (hover? 'mdi-chevron-double-right' :'mdi-chevron-double-left')}}</v-icon>
                        </v-btn>
                    </v-hover>
                    <v-btn icon @click.stop="dialogPath=true">
                        <v-icon> mdi-routes</v-icon>
                    </v-btn>
                    <dialogPathSetting v-model="dialogPath" :path="element.path" @submit="submitDialog"/>
                    <v-toolbar-title>HW Element</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-toolbar v-else hide-on-scroll dense flat>
                    <v-toolbar-title>HW Element</v-toolbar-title>
                </v-toolbar>
                <v-card-text  v-if="iselementOpenClose">
                    <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                @input='inputHWElementName' outlined dense></v-text-field>
                    <v-row style="height: 70px">
                        <v-col cols="10">
                            <v-text-field v-model="element.category" readonly @click="setHWCategorySelect()" clearable @click:clear='clearHWCategoryRef()' label="HW Category" style="height:25px;" outlined dense class="lable-placeholer-color"></v-text-field>
                        </v-col>
                        <v-col cols="2">
                            <v-menu>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn color="deep-purple accent-4" :id="element.uuid+'/hwcatrory'" dark icon v-bind="attrs" v-on="on" @click="setHWCategoryList()">
                                        <v-icon>mdi-menu-down-outline</v-icon>
                                    </v-btn>
                                </template>
                                <v-list>
                                    <v-list-item v-for="(item, i) in selHWCategory" :key="i" link @click="setHWCategory(item)">
                                        <v-list-item-title>{{ item.name }}</v-list-item-title>
                                    </v-list-item>
                                    <v-btn outlined color="indigo" dense text small block @click="newHWCategory" >
                                        <v-icon >mdi-plus</v-icon>New Item
                                    </v-btn>
                                </v-list>
                            </v-menu>
                        </v-col>
                    </v-row>
                    <v-card outlined class="mx-auto">
                        <div class="subtitle-2" style="height:20px">
                            <v-hover v-slot="{ hover }">
                                <v-btn text @click="showAttributeItem" x-small color="indigo">
                                    <v-icon>{{ isAttributeOpenClose? (hover? 'mdi-chevron-double-left' :'mdi-chevron-double-right') : (hover? 'mdi-chevron-double-right' :'mdi-chevron-double-left')}}</v-icon>
                                </v-btn>
                            </v-hover>
                            HW Attribute Value
                            <v-btn @click="isCheckAttribute" text x-small color="indigo" v-if="isAttributeOpenClose">
                                <v-icon>mdi-check</v-icon>
                            </v-btn>
                            <v-btn  v-if="isAttributeOpenClose && isdeleteAttributeItem" @click="deleteAttribute" text x-small color="indigo">
                                <v-icon>mdi-minus</v-icon>
                            </v-btn>
                        </div>
                        <v-card-text v-if="isAttributeOpenClose">  
                            <v-data-table v-model="selectdeleteAttribut" :headers="headersAttribut" :items="AttributeItem" :id="element.uuid+'/attributetable'"
                                    :show-select="isdeleteAttributeItem" item-key="attr" height="100px" dense hide-default-footer >
                                <template v-slot:item.data-table-select="{ isSelected, select }">
                                    <v-simple-checkbox color="green" :value="isSelected" @input="select($event)"></v-simple-checkbox>
                                </template>
                                <template v-if="!isdeleteAttributeItem" v-slot:body="{ items, headers }">
                                    <tbody>
                                        <tr v-for="(item,idx) in items" :key="idx" >
                                            <td v-for="(header,key) in headers" :key="key">
                                                <v-edit-dialog v-if="header.value != 'sort'" persistent cancel-text='Ok' save-text="Cancel" @open="openAttribute(idx)" @cancel="editAttribute(idx)" @save="cancelAttribute" large >
                                                    {{item[header.value]}}
                                                    <template v-slot:input>
                                                        <br>
                                                        <v-autocomplete v-model='editAttributeItem.attr' label='HW Attribute' :items='selHWAttribute' item-text='name' item-value="uuid" class="lable-placeholer-color"
                                                                   return-object :readonly="!isEditingHWAttribute" @click="setHWAttributeSelect()" @blur="isEditingHWAttribute=true">
                                                             <template v-slot:append-item>
                                                                <v-btn outlined color="indigo" dense text small block @click="newHWAttribute">
                                                                    <v-icon >mdi-plus</v-icon>New Item
                                                                </v-btn>
                                                            </template>
                                                        </v-autocomplete>
                                                        <v-select v-model="editAttributeItem.vt" :items="attributeType" label="type" @click="setactiveUUID()" outlined dense style="height: 45px;"></v-select>
                                                        <v-text-field v-model="editAttributeItem.v" label="Size" placeholder="String" @click="setactiveUUID" style="height: 45px;" outlined dense class="lable-placeholer-color"></v-text-field>
                                                    </template>
                                                </v-edit-dialog>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th colspan="3">
                                                <v-edit-dialog  large persistent cancel-text='Ok' save-text="Cancel" @cancel="addAttribute()" @save="cancelAttribute"> 
                                                    <v-btn outlined color="indigo" dense text small block width="270px" >
                                                        <v-icon >mdi-plus</v-icon>New Item
                                                    </v-btn>
                                                    <template v-slot:input>
                                                        <br>
                                                        <v-autocomplete v-model='editAttributeItem.attr' label='HW Attribute' :items='selHWAttribute' item-text='name' item-value="uuid" class="lable-placeholer-color"
                                                                   return-object :readonly="!isEditingHWAttribute" @click="setHWAttributeSelect()" @blur="isEditingHWAttribute=true"
                                                                   @click:clear='clearHWAttribute' clearable>
                                                             <template v-slot:append-item>
                                                                <v-btn outlined color="indigo" dense text small block @click="newHWAttribute">
                                                                    <v-icon >mdi-plus</v-icon>New Item
                                                                </v-btn>
                                                            </template>
                                                        </v-autocomplete>
                                                        <v-select v-model="editAttributeItem.vt" :items="attributeType" label="type" @click="setactiveUUID()" outlined dense style="height: 45px;"></v-select>
                                                        <v-text-field v-model="editAttributeItem.v" label="Size" placeholder="String" @click="setactiveUUID" style="height: 45px;" outlined dense class="lable-placeholer-color"></v-text-field>
                                                    </template>
                                                </v-edit-dialog>
                                            </th>
                                        </tr>
                                    </tbody>
                                </template>
                            </v-data-table>
                        </v-card-text>
                    </v-card>
                </v-card-text>
                <v-card-text v-else>
                    <v-text-field v-model="element.name" :label="'name  <'+element.path +'>'" :rules="rules.name" placeholder="String" style="height: 45px;" class="lable-placeholer-color"
                                readonly outlined dense></v-text-field>
                </v-card-text>
            </v-card>
        </v-container>
    </div>
</template>

<script>
import constant from "../store/constants.js"
import { EventBus } from "../main.js"
import dialogPathSetting from '../components/dialogPathSetting.vue'

export default {
    props: ['element', 'isDatailView'],
    components:{dialogPathSetting},
    computed: { 
        activeUUID() {
            return this.$store.state.activeUUID
        },
        detailViewUUID() {
            return this.$store.state.detailViewUUID
        }
    },
    watch: {
        activeUUID(val) {
            this.setToolbarColor(val)
        },
        detailViewUUID(val) {
            this.setToolbarColorDetailView(val)
        }
    },
    created() {
        this.setToolbarColor(this.$store.state.activeUUID)
    },
    data() {
        return {
            rules: { name:  [val => (val || '').length > 0 ], },
            dialogPath : false,
            colorToolbar: "#6A5ACD",
            iselementOpenClose: true,
            selHWCategory: this.$store.getters.getHWCategory,
            selHWAttribute: this.$store.getters.getHWAttribute,
            isEditingHWAttribute: true,
            isAttributeOpenClose: true,
            isdeleteAttributeItem: false,
            headersAttribut: [
                { text: 'HW Attribute', align: 'start', sortable: false, value: 'attr' },
                { text: 'Type',  sortable: false, value: 'vt' },
                { text: 'Size', sortable: false, value: 'v' },
            ],
            attributeType: [ 'EEPROM', 'FLASH', 'RAM','ROM',],
            selectdeleteAttribut: [],
            AttributeItem: [],
            editAttributeItem: { attr: null, vt:'', v:''},
            defaultAttributeItem: { attr: null, vt:'', v:''}

        }
    },
    mounted () {
        this.$nextTick(() => {
            if(this.element.attribute != undefined) {
                this.AttributeItem = this.element.attribute.slice()
            }
        })
    },
    methods: {
        submitDialog(element) {
            this.element.path = element
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
        },
        setToolbarColor(activeid) 
        {
            if(this.element.uuid == activeid) {
                this.colorToolbar = "#FF1493" 
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        setToolbarColorDetailView(isdetail) {
            if(this.element.uuid == isdetail) {
                this.colorToolbar = "#B0E0E6" 
            } else {
                this.colorToolbar = "#6A5ACD"
            }
        },
        shoeModeDeclaration() {
            this.iselementOpenClose = this.iselementOpenClose ? false : true
        },
        showAttributeItem() {
            this.isAttributeOpenClose = this.isAttributeOpenClose ? false : true
        },
        inputHWElementName() {
            this.$store.commit('editHWElement', {compo:"Name", uuid:this.element.uuid, name:this.element.name} )
            this.$store.commit('changePathElement', {uuid:this.element.uuid, path: this.element.path, name: this.element.name} )
        },
        setHWCategory(item) {
            if( this.element.category != item.name) {
                var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/hwcatrory')
                if (endLine != undefined && endLine != item.uuid) {
                    //기존꺼 삭제해야한다 vuex에서도 삭제하고 mainview에서도 삭제하고 
                    this.deleteLine(this.element.uuid+'/hwcatrory')
                }
                //새로 추가해준다
                this.newLine(this.element.uuid+'/hwcatrory', this.element.uuid+'/hwcatrory', item.uuid)
                this.element.category = item.name
            }
            this.setactiveUUID()
        },
        setHWCategorySelect() {
            var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/hwcatrory')
            if (endLine == undefined) {
                endLine = this.$store.getters.getHWCategoryPath(this.element.category)
            }
            if (endLine != null) {
                this.$store.commit('setDetailView', {uuid: endLine, element: constant.HWCategory_str} )
            }
        },
        clearHWCategoryRef() {
            this.element.category = null
            var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/hwcatrory')
            if (endLine != undefined) {
                this.deleteLine(this.element.uuid+'/hwcatrory')
            }
        },
        setHWCategoryList() {
            this.selHWCategory = this.$store.getters.getHWCategory
            this.setactiveUUID()
        },
        newHWCategory() {
            // new HW Category
            this.$store.commit('addElementHWCategory', { //category 는 null해줘야한다. clearable하면 값이 null변하기 때문에 
                    name: this.$store.getters.getNameHWCategory,  input: false, path: '',
                    top: this.element.top+100, left: this.element.left+ 300 , zindex: 10,  icon:"mdi-clipboard-outline", validation: false,
            })

            EventBus.$emit('add-element', constant.HWCategory_str)
            EventBus.$emit('add-element', constant.Machines_str)
            this.$store.commit('editHWElement', {compo:"z", uuid:this.element.uuid, zindex:2} )
        },

        isCheckAttribute() {
            if (this.isdeleteAttributeItem == true) {
                this.isdeleteAttributeItem = false
                this.selectdeleteAttribut = []
            } else {
                this.isdeleteAttributeItem = true
            }
        },
        deleteAttribute() {
            if (this.isdeleteAttributeItem == true) {
                this.AttributeItem = this.AttributeItem.filter(item => {
                         return this.selectdeleteAttribut.indexOf(item) < 0 })
                this.isdeleteAttributeItem = false
                this.selectdeleteAttribut = []
                this.inputAttribute()
            } 
        },
        inputAttribute() {
            this.$store.commit('editHWElement', {compo:"Attribute", uuid:this.element.uuid, attribute:this.AttributeItem} )
        },
        setHWAttributeSelect() {
           if (this.isEditingHWAttribute == true) {
                if (this.editAttributeItem.attr != null) {
                    this.$store.commit('setDetailView', {uuid: this.editAttributeItem.attr.uuid, element: constant.HWCategory_str} )
                }
                 this.setHWAttributeList()
                this.isEditingHWAttribute = false
            } else {
                this.isEditingHWAttribute = true
            }
        },
        clearHWAttribute() {
            this.isEditingHWAttribute = true
            this.editAttributeItem.attr = null
        },
        setHWAttributeList() {
            this.selHWAttribute = this.$store.getters.getHWAttribute
            this.setactiveUUID()
        },
        newHWAttribute() {
            // new HW Attribute
            EventBus.$emit('add-element', constant.HWCategory_str)
            EventBus.$emit('add-element', constant.Machines_str)
            this.$store.commit('editHWElement', {compo:"z", uuid:this.element.uuid, zindex:2} )
        },
        cancelAttribute() {
            this.editAttributeItem = Object.assign({}, this.defaultAttributeItem)
            this.setactiveUUID()
        },
        editAttribute(idx) {
            var endLine = this.$store.getters.getChangeEndLine(this.element.uuid+'/attributetable-'+idx)
            console.log(endLine + '+' + this.editAttributeItem.attr.uuid)
            if (endLine != undefined && this.editAttributeItem.attr == null) {
                this.deleteLine(this.element.uuid+'/attributetable-'+idx)
                this.AttributeItem[idx].attr = null
            } else if (endLine != undefined && endLine != this.editAttributeItem.attr.uuid) {
                //기존꺼 삭제해야한다 vuex에서도 삭제하고 mainview에서도 삭제하고 
                this.deleteLine(this.element.uuid+'/attributetable-'+idx)
                this.newLine(this.element.uuid+'/attributetable-'+idx, this.element.uuid+'/attributetable', this.editAttributeItem.attr.uuid)
                this.AttributeItem[idx].attr = this.editAttributeItem.attr.name
            } else if (endLine == undefined && this.editAttributeItem.attr != null) {
                this.newLine(this.element.uuid+'/attributetable-'+idx, this.element.uuid+'/attributetable', this.editAttributeItem.attr.uuid)
                this.AttributeItem[idx].attr = this.editAttributeItem.attr.name
            }

            this.AttributeItem[idx].v = this.editAttributeItem.v
            this.AttributeItem[idx].vt = this.editAttributeItem.vt
            this.cancelAttribute()
            this.inputAttribute()

        },
        openAttribute(idx) {
            this.selHWAttribute = this.$store.getters.getHWAttribute
            if ( this.AttributeItem[idx].attr != null) {
                this.editAttributeItem.attr = { name: this.AttributeItem[idx].attr, uuid: this.$store.getters.getChangeEndLine(this.element.uuid+'/attributetable-'+idx) }
            }
            console.log(this.AttributeItem[idx].attr)
            console.log(this.editAttributeItem.attr.name)
            this.editAttributeItem.vt = this.AttributeItem[idx].vt
            this.editAttributeItem.v = this.AttributeItem[idx].v
        },
        addAttribute() {
            if( this.editAttributeItem.attr != null) {
                var datacount
                if(this.AttributeItem == undefined) {
                    datacount = 0
                }else {
                    datacount = this.AttributeItem.length
                }
                this.newLine(this.element.uuid+'/attributetable-'+datacount, this.element.uuid+'/attributetable', this.editAttributeItem.attr.uuid)
                this.editAttributeItem.attr = this.editAttributeItem.attr.name
            }
            const addObj = Object.assign({}, this.editAttributeItem);
            this.AttributeItem.push(addObj);
            this.cancelAttribute()
            this.inputAttribute()
        },

        setactiveUUID() {
            this.$store.commit('setuuid', {uuid: this.element.uuid} )
            this.$store.commit('editHWElement', {compo:"z", uuid:this.element.uuid, zindex:10} )
        },
        deleteLine(fineLine) {
            var linenum = this.$store.getters.getconnectLineNum(fineLine)
            if (linenum != -1) {
                EventBus.$emit('delete-line', linenum)
                this.$store.commit('deletConnectionline', {startnum: linenum} )
            }
        },
        newLine(startLine, drawLine, endLine) {
            this.$store.commit('setConnectionline', {start: startLine, end: endLine} )
            EventBus.$emit('new-line', drawLine, endLine)
        },
    }
}
</script>

<style>
</style>
